// @flow

/**
 * About.js - контейнер для страницы "О продукте"
 * @module containers/About
 * @requires module:components/Nav
 * */

import React, {Component} from 'react'

import {Nav} from '../../components/Nav/Nav'

/**
 * */
export class About extends Component<void, void> {
    /**
     * Отрисовка компонента при изменении состояния
     * */
    render() {
        return (
        <div className="About">
            <div className="container-fluid">
                <div className="row">
                    <div className="col-1 p-0">
                        <Nav/>
                    </div>
                    <div className="col-11 Dashboard__content">
                        <h2>О продукте</h2>
                        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Autem dicta dolore doloribus facere fuga impedit iure labore, molestiae molestias officia officiis quae quia, quibusdam sequi similique sunt voluptate! Animi, molestias.</p>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}

export default About



